/* 去除 body 的边距 */
body {
	margin: 0px;
	padding: 0px;
	border: 0px;
	overflow: scroll;
}

html,
body {
	width: 100%;
	height: 100%;
}

#background-image {
	width: 100%;
	height: 100%;
}

#root {
	overflow: hidden;
}

#card {
	backdrop-filter: blur(8px);
	border-radius: 12px;
	overflow: hidden;
}